

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";

import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
// import ville from "../../dict/ville.js";
// import { storage } from "../../../firebase/firebase";
import ModalMessage from "../../../pages/messages/ModalMessage"
const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)

class ModifierApropos extends Component {
    constructor(props) {
        super(props)
        this.state = {
          ok:"",
          show:false,
          success:true,
          boutton:"OK",
          message:"A propos Modifié avec succès !!",
          titre:"Message modification de a propos"
        }
    }

    componentDidMount() {
      var id=localStorage.getItem("idApropos")
    
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
   
      const requestOptions = {
        method: 'GET',
        headers: { 
        'Authorization':'Bearer '+token.token,
      }
    };
  
    fetch(conf.url+"acceuil/propos/propos/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       $("#titreapropos").val(data.titre)
       $("#textapropos").val(data.text)
       $("#urlvideoapropos").val(data.urlVideo)
       $("#telephone").val(data.telephone)
       $("#email").val(data.email)
       $("#adresse").val(data.adresse)
     }
     
    );
  
  
    }

    
  handleAddPropos=()=>{

    var id=localStorage.getItem("idApropos")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var data= {
      "id":id,
      "titre": $("#titreapropos").val(),
      "text": $("#textapropos").val(),
      "urlVideo":$("#urlvideoapropos").val(),
      "telephone": $("#telephone").val(),
      "email": $("#email").val(),
      "adresse":$("#adresse").val(),
  
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };

  fetch(conf.url+"propos/update",requestOptions)
  .then((response) => response.json())
  .then((data) => {
     console.log("okkk====>",data)
     

    try{
      if(data.id){
       this.setState({show:true,success:true});
      }else{
       this.setState({show:true,success:false});
      }
   }catch(err){
     this.setState({show:true,success:false});
   }
  
   }
   
  );

  }
 
   
  handleValidate=()=>{
    if(this.state.success){
      this.setState({ok: <Redirect to='/dynamic-form-accueil'/>});
    }else{
      this.setState({show:false})
    }
    
  }
  render() {

  const { pick } = this.state
  const { selectedGroup } = this.state
  const { selectedLangue } = this.state
  const animatedComponents = makeAnimated();

  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}

             <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Modifer à propos</h1>
                  {/* </CCardHeader> */}


                  <CardBody>
                    
                    <Row  className="mt-3"> 
                      <Col md="12">
                        <label>Titre à propos</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="titreapropos"
                          placeholder="Titre à propos"
                        />
                      </Col>
                    </Row>


                    <Row className="mt-3">
                      <Col md="12">
                        <label>Text à propos</label>
                        <textarea  className="form-control" id="textapropos" placeholder="Text à propos" />
                      </Col>
                    </Row>

                    <Row className="mt-3">
                    <Col md="4">
                      <label>Vidéo propos</label>
                      <Input
                          className="form-control"
                          type="text"
                          id="urlvideoapropos"
                         
                        />
                      </Col>
                    </Row>

                    <Row className="mb-5"> 
                      <Col md="4" className="mt-3">
                        <label>Téléphone</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="telephone"
                          placeholder="Téléphone"
                        />
                      </Col>
                      <Col md="4" className="mt-3">
                        <label>Email</label>
                        <Input
                          className="form-control"
                          type="email"
                          id="email"
                          placeholder="Email"
                        />
                      </Col>
                      <Col md="4" className="mt-3">
                        <label>Adresse</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="adresse"
                          placeholder="Adresse"
                        />
                      </Col>
                    </Row>
                    
                    <Row className="justify-content-start mt-5">
                      <Col md="6" className="mt-1">
                        <Button  color="secondary" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                      <Col md="6" className="mt-1">
                        <Button id="initial" onClick={this.handleAddPropos}   color="success" block className="b-lg" >
                          Modifer à propos
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                    </Row>


                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default ModifierApropos